import React from 'react';
import logo from '../../logo.png'
import './Header.css';

function Header() {

      
    return (  <>
    
    <nav className="navbar bg-light shadow-sm p-0">
  <div className="container-fluid">
    <a className="navbar-brand" href="#">
      <img src={logo} alt="Logo" width="50" height="60" className="d-inline-block align-text-top"/>
    </a>
  </div>
</nav>

    </>);
}

export default Header;