import React from 'react';
import { useFormik } from "formik";
import { useEffect, useRef, useState } from "react";
import { useNavigate } from "react-router-dom";
import Joi from "joi";
import './Wellcome.css'
export interface IErrors {
    [key: string]: string;
}

function Wellcome() {

    const navigate = useNavigate();
    const inputRef = useRef<null | HTMLInputElement>(null);
    const [errorMsg, setErrorMsg] = useState('');


    useEffect(() => {
        if (inputRef && inputRef.current) {
            inputRef.current.focus();
        }
    }, []);

    const formik = useFormik({

        initialValues: {
            phone: '',
        },

        validate: values => {
            const errors: IErrors = {};

            const schema = Joi.object().keys({
                phone: Joi.string().required().min(10).max(10),
            });

            const { error } = schema.validate(values);

            if (error) {
                error.details.forEach(item => {
                    if (item.context) {
                        const key = item.context.key + '';
                        errors[key] = item.message;
                    }
                })
            };

            return errors;
        },

        onSubmit: values => {
        //     const res = authRequest('users/login', values);

        //     res.then(res => res.json())
        //         .then(json => {
        //             if(json.error){
        //                 setErrorMsg(json.error);
        //                 return;
        //             }
        //             localStorage.setItem(TOKEN_KEY, json.token);
        //             localStorage.setItem(USER_ID, json.id);
                    navigate('/schedule');
        //         })


        //         res.catch(json=>{
        //             console.log( json.error);
        //         })
        },
    }
    );


    return (
     <>
    

    <form
        onSubmit={formik.handleSubmit}
        className="p-3 form-max-w m-auto d-block"
    >
                <h1 className="text-center" >מבוטח יקר,</h1>
                <h2 className="mb-3">לתיאום  סקר הכנס מס' טלפון נייד</h2>

        <div className="mb-3">
            <input
                ref={inputRef}
                className="form-control"
                type="text"
                placeholder="נייד"
                id="phone"
                name="phone"
                autoComplete="phone"
                onChange={formik.handleChange}
                value={formik.values.phone}
                onBlur={formik.handleBlur}
            />
        </div>
        {
            formik.touched.phone && formik.errors.phone ? (
                <div className="text-danger">
                 יש להכניס מספר נייד בן 10 ספרות</div>
            ) : null
        }
<div className='d-flex justify-content-center'>
<button
            type="submit"
            className="btn btn-primary btn-lg w-50 m-auto bg-secondary">
            המשך
        </button>
</div>
      

        {
            (errorMsg.length>1) && (
                <div className="text-danger">
                    {errorMsg}</div>
            ) 
        }
    </form>
    
    </> );
}

export default Wellcome;