
export const TOKEN_KEY = 'token';
export const USER_ID = 'user_id';
export const dateSelected = 'date';
export const houerSelected = 'hour';

export const getToken = function (): string {
    return localStorage.getItem(TOKEN_KEY) || '';
}

export const getDate = function (): string {
    return localStorage.getItem(dateSelected) || '';
}

export const getHour = function (): string {
    return localStorage.getItem(houerSelected) || '';
}

export const verifyToken = function (): boolean {
    const token = getToken();
    return token.length > 0;
}

export const getUserId = function (): string {
    return localStorage.getItem( USER_ID) || '';
}