import React, { useState } from "react";
import DatePicker, { registerLocale } from "react-datepicker";
import he from "date-fns/locale/he";
import "./Schedule.css";
import InsuredDetails from "../InsuredDetails/InsuredDetails";
import Hour from "./Hour/Hour";
import { useNavigate } from "react-router-dom";
import { dateSelected, houerSelected } from "../../services/auth";

registerLocale("he", he);

function Schedule() {
  const navigate = useNavigate();
  const [startDate, setStartDate] = useState(new Date());
  const [freeHours, setFreeHours] = useState<Array<string>>([]);
  const [selectedHour, setSelectedHour] = useState("");
  const [errmsg, setErrmsg] = useState("");

  //   function setHour(e: any) {
  //     setSelectedHour(e.target.value);
  //   }

  function submit() {
    if (startDate && selectedHour) {
        localStorage.setItem(dateSelected, startDate.toLocaleDateString("he-IL"));
                    localStorage.setItem(houerSelected, selectedHour);
      navigate("/goodby");
      return;
    } else {
      setErrmsg("אירעה שגיאה בתיאום הסקר. נסה שוב. ");
    }
  }
  return (
    <div className="w-70 m-3 mx-auto bg-light d-flex flex-column align-items-center">
      <InsuredDetails />

      <h4 className="mt-4 mb-0">בחר תאריך ושעה:</h4>
      <DatePicker
        locale="he"
        selected={startDate}
        onChange={(date: Date) => {
          setStartDate(date);
          if (date.toDateString() === "Tue Feb 14 2023") {
            setFreeHours(["15:30", "17:00"]);
          } else if (date.toDateString() === "Thu Feb 16 2023") {
            setFreeHours(["14:30", "19:00", "20:00"]);
          } else {
            setFreeHours([]);
          }
        }}
        inline
        includeDates={[
          new Date("2023-02-14"),
          new Date("2023-02-13"),
          new Date("2023-02-16"),
          new Date("2023-03-14"),
        ]}
      />

      {/* <>
    שעות פנויות בתאריך {startDate}:
    </> */}

      {freeHours.length === 0 && (
        <div className="alert alert-warning w-50" role="alert">
          אין שעות פנויות בתאריך זה
        </div>
      )}

      {freeHours.length > 0 && (
        <select
          className="form-select selectList"
          //   aria-label="Default select example"
          value={selectedHour}
          onChange={(e) => setSelectedHour(e.target.value)}
        >
          {freeHours.map((hour) => hour && <Hour key={hour} data={hour} />)}
        </select>
      )}

      {errmsg && (
        <div className="alert alert-warning w-50" role="alert">
          {errmsg}
        </div>
      )}
      <div className="d-flex w-50  bg-secondary justify-content-left rounded-3 m-4">
        <button
          onClick={() => submit()}
          type="submit"
          className="btn btn-light my-3 w-50 btn-lg mx-auto"
        >
          תאם סקר
        </button>
      </div>
    </div>
  );
}

export default Schedule;
