import React from "react";
import InsuredDetails from "../InsuredDetails/InsuredDetails";
import { useNavigate } from "react-router-dom";
import { dateSelected, houerSelected } from "../../services/auth";

function Goodby() {
    const navigate = useNavigate();
const hour =localStorage.getItem(houerSelected);
const date =localStorage.getItem(dateSelected);
    return ( <>
    
    <div className="sm-w-100 m-w-70 m-3 mx-auto bg-light d-flex flex-column align-items-center">
      <InsuredDetails />

      <h5 className="alert alert-success m-5" role="alert">
      תואם סקר תכולה לתאריך {date} בשעה: {hour} עם הסוקר נחום
      </h5>
      <div className="d-flex w-50  bg-secondary justify-content-left rounded-3 m-4">
      <button
          onClick={() => navigate("/schedule")}
          type="submit"
          className="btn btn-light my-3 w-50 btn-lg mx-auto"
        >
            לתיאום מחדש
        </button>
        </div>
        </div>

    </> );
}

export default Goodby;